<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process251"></a>

<p><br></p>
<p><span class="rvts17">像钟表一样奔跑（</span><span class="rvts50">Running Like Clockwork</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:277,Y:824</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：红宝石（</span><span class="rvts20">Ruby</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助红宝石拯救小鬼王国</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1387.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">首先该任务可以从小鬼祭坛处接到（小鬼祭坛位于兽人祭坛和精灵祭坛中部，不太显眼）。然而，也可以在距离月亮神庙传送点不远处的台阶下这里接到。</span>
<br><span class="rvts21">红宝石正在受两只狼的欺负，尝试使用</span><span class="rvts20"></span><span class="rvts21">动物伙伴</span><span class="rvts20"></span><span class="rvts21">天赋给这两只狼一些肉食，使它们停止对红宝石的蹂躏。或者通过战斗，击杀两只狼。</span>
<br><span class="rvts21">之后与红宝石谈话，它会告诉你小鬼王国似乎发生了一些不太好的事。它会把你传送到小鬼王国里。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1388.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">刚到小鬼王国，所有队伍成员直接身上自带</span><span class="rvts20">DEBUFF</span><span class="rvts21">减速效果，这样以来显得行动十分缓慢。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1389.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">第一处解密比较简单，开场区域一共有</span><span class="rvts20">4</span><span class="rvts21">个机关，全部踩上去才能激活远处的大门。可以使用队伍中力量较高的角色，找一些</span><span class="rvts20"></span><span class="rvts21">沉重的金属箱</span><span class="rvts20"></span><span class="rvts21">压在机关上面当做填充物。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1390.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">到达第二处解密，会遇到很多喷毒喷火陷阱。解密也十分简单，使队伍中一名角色站在离门最近的那个陷阱上，堵住雾气，另外一名角色使用钥匙或开锁技能，打开这里的</span><span class="rvts20"></span><span class="rvts21">飞速运转协议（</span><span class="rvts20">HYPERDRIVE PROTOCAL</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">。之后，队伍的所有角色将会由原来的减速状态变为加速状态。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1391.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最后到达终点，会触发</span><span class="rvts20"></span><span class="rvts21">自爆协议</span><span class="rvts20"></span><span class="rvts21">。此时只有一次移动机会到光球开关处，因此尽可能使用</span><span class="rvts20"></span><span class="rvts21">跳跃</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">传送</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">急促</span><span class="rvts20"></span><span class="rvts21">等技能让队伍中的一名角色关闭该装置。</span>
<br><span class="rvts21">如果失败的话，那么整个小鬼神殿将会充满死亡烟雾，所有不死角色都将瞬间毙命。</span>
<br><span class="rvts21">顺利到达终点并关闭装置后，与装置对话，将完成该支线任务。另外，不要忘记装置远处有小鬼祭坛，通过祈祷，可以获得祭坛的日月派别。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



